body {
  background: url(https://cdn.mobilesyrup.com/wp-content/uploads/2019/08/code-ninjas.jpg) no-repeat center fixed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0px;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: cornflowerblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ol, ul {
  padding-left: 30px;
}

.board.row {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  text-align: center;
  font-size: 30px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 75px;
  font-weight: bold;
  line-height: 34px;
  height: 120px;
  margin-right:-1px ;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 120px;
}

.square:focus {
  outline: none;
}

.game {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 600px;
}

.game-board {
  margin: auto;
}

@media only screen and (max-width: 767px) {
  body { 
    background-color: darkcyan;
    border: none;
    margin: 0px;
    padding: 0px;
    width: 100%;
    overflow-x: hidden;
  }

  .game {
    overflow-x: hidden;
  }

  .sweet-alert-lg {
    width: 200px,
  }

  .status {
    margin-bottom: 5%;
    text-align: center;
    font-size: 150%;
  }

  .game-board {
    margin-left: 3%;
    margin-top: 25%;
    height: auto;
    overflow-x: hidden;
  }

  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 30%;
    text-align: justify;
    font-weight: bold;
    height: 70px;
    padding: 0;
    text-align: center;
    width: 75px;
    overflow-x: hidden;
  }
}
